export default {
    isAuthenticated(state) {
      return state.isAuthenticated;
    },
    news(state) {
      return state.news;
    },
    addFavorite(state, news) {
      state.favorites.push(news);
    },
    updateFavorite(state, { id, updatedNews }) {
      const index = state.favorites.findIndex(news => news.id === id);
      if (index !== -1) {
        state.favorites.splice(index, 1, updatedNews);
      }
    },
    removeFavorite(state, id) {
      state.favorites = state.favorites.filter(news => news.id !== id);
    }
  };
  